body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.resume {
    color: white;
    margin-left: 100px;
    margin-bottom: calc(47% - 25px);
}
.resume:hover{
    color: darkgray;
}
.footer {
    bottom: 0;
}
.color-background {
    margin-bottom: calc(47% - 25px);
}
.navbar{
    background: rgba(252, 252, 252, 0) !important;
}

.navbar-toggler-icon {
    order:0;
    float: left;
  }
.navbar-link {
    padding-right: .5rem;
    padding-left: .5rem;
}
.navbar-link:hover {
    cursor: pointer;
    color: darkgray;
}
a:hover {
    color: darkgray;
}
.wrapper{
    background-image: url(/static/media/slc.de5158f7.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    background-attachment: fixed;
    overflow-y: auto;
    color: white;
    font-family: 'Ubuntu', sans-serif;
    height: 100vh;
}
.footer {
    background: rgba(252, 252, 252, 0) !important;
    inline-size: auto;
    bottom: 0;
    left: 0;
    padding: 3px;
    margin-top: 5px;
  }
